export class EditPostComponent {
    constructor(activatedRoute, posts, ep, title, router, sanitized) {
        this.posts = posts;
        this.ep = ep;
        this.router = router;
        this.sanitized = sanitized;
        /**
         * Data coming from the user
         */
        this.postChanges = new Subject();
        this.postPreview = this.postChanges.pipe(debounceTime(300), map(post => {
            return this.sanitized.bypassSecurityTrustHtml(snarkdown((post && post.body) || ''));
        }));
        this.postData = activatedRoute.params.pipe(switchMap(params => {
            if (!params['id']) {
                console.error('No post specified');
                return;
            }
            else if (params['id'] === 'new') {
                return observableOf(new Post());
            }
            return posts.postMap.pipe(map(postListObject => {
                console.log('Looking for post from', params, postListObject);
                console.log(postListObject);
                const item = postListObject[params['id']];
                if (item) {
                    title.setTitle('Edit ' + item.title + ' | fluin.io blog');
                    this.contentChange(item);
                }
                return item;
            }), tap(post => this.contentChange(post)));
        }));
    }
    contentChange(post) {
        this.postChanges.next(post);
    }
    save(post) {
        this.ep.save(post);
        this.router.navigateByUrl('/admin');
    }
    onKey(event, post) {
        if (event.ctrlKey && event.key === 's') {
            this.ep.save(post);
            event.preventDefault();
        }
    }
    delete(post) {
        this.ep.delete(post);
        this.router.navigateByUrl('/admin');
    }
}
